<template>
  <div class="embed-responsive embed-responsive-16by9">
    <iframe
      @load="load"
      class="embed-responsive-item"
      :src="links.ubicacion"
    ></iframe>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";

export default {
  created() {
    this.setTitle();
  },
  mounted() {
    this.loader = this.$loading.show();
  },
  beforeRouteLeave(to, from, next) {
    // Oculta el loader antes de cambiar de pestaña
    this.loader.hide();
    next();
  },
  computed: {
    ...mapState(["links"]),
  },
  watch: {
    ["$i18n.locale"]() {
      this.setTitle();
    },
  },
  methods: {
    ...mapMutations(["SET_TITLE"]),
    load() {
      this.loader.hide();
    },
    setTitle() {
      this.SET_TITLE(this.$i18n.t("titulo"));
    },
  },
};
</script>

<style></style>

<i18n>
{
  "es": {
    "titulo": "Ubicación"
  },
  "en": {
    "titulo": "Location"
  }
}
</i18n>
