<template>
  <div>
    <div class="row">
      <div
        v-for="(integrante, index) in integrantes"
        :key="index"
        class="col-sm-3 my-3"
      >
        <Ficha
          :integrante="integrante"
          :colorFondo="getColorFondo(index)"
          @mostrarDetalle="show"
        />
      </div>
    </div>

    <!-- Modal -->
    <div class="modal fade" ref="modal" tabindex="-1" role="dialog">
      <div class="modal-dialog modal-dialog-centered modal-lg" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 v-if="integranteSeleccionado" class="modal-title">
              {{ integranteSeleccionado.nombre }} -
              {{ integranteSeleccionado.email }}
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div v-if="integranteSeleccionado" class="modal-body">
            <div v-html="integranteSeleccionado.detalle"></div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-dismiss="modal"
            >
              {{ $t("Cerrar") }}
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- Fin Modal -->
  </div>
</template>

<script>
import { mapMutations } from "vuex";
import Ficha from "@/components/integrantes/Ficha";
import integrantes_es from "@/data/integrantes.es";
import integrantes_en from "@/data/integrantes.en";

const coloresFondo = [
  "bg-success",
  "bg-primary",
  "bg-warning",
  "bg-danger",
  "bg-dark",
];

export default {
  components: {
    Ficha,
  },
  data() {
    const integrantes = this.getIntegrantes();
    return {
      integrantes,
      integranteSeleccionado: null,
    };
  },
  created() {
    this.setTitle();
  },
  watch: {
    locale() {
      this.integrantes = this.getIntegrantes();
      this.setTitle();
    },
  },
  methods: {
    ...mapMutations(["SET_TITLE"]),
    getColorFondo(index) {
      const nextIndex = index % coloresFondo.length;
      return coloresFondo[nextIndex];
    },
    show(integrante) {
      this.integranteSeleccionado = integrante;
      $(this.$refs.modal).modal("show");
    },
    getIntegrantes() {
      return this.$i18n.locale === "en" ? integrantes_en : integrantes_es;
    },
    setTitle() {
      this.SET_TITLE(this.$i18n.t("CVs"));
    },
  },
};
</script>

<style scoped>
* >>> .card-flip > div {
  backface-visibility: hidden;
  transition: transform 300ms;
  transition-timing-function: linear;
  width: 100%;
  height: 100%;
  margin: 0;
  display: flex;
}

* >>> .card-front {
  transform: rotateY(0deg);
}

* >>> .card-back {
  transform: rotateY(180deg);
  position: absolute;
  top: 0;
}

* >>> .card-flip:hover .card-front {
  transform: rotateY(-180deg);
}

* >>> .card-flip:hover .card-back {
  transform: rotateY(0deg);
}
</style>

<i18n>
{
  "es": {
    "CVs" : "CV de los Integrantes del Juzgado",
    "Cerrar": "Cerrar"
  },
  "en": {
    "CVs" : "Team CV's",
    "Cerrar": "Close"
  }
}
</i18n>
